import Vue from 'vue';
import vuetify from './plugins/vuetify';
import VueRouter from 'vue-router';
import VueTypedJs from 'vue-typed-js';
import FlipCountdown from 'vue2-flip-countdown';

import App from './App.vue';

// Home Components
import Welcome from '@/components/Welcome';
import PrivacyPolicy from '@/components/PrivacyPolicy';
import NotFound from '@/components/NotFound';

// Customized Components
import Divider from '@/components/material/Divider';
import TextContent from '@/components/material/TextContent';
import TextHint from '@/components/material/TextHint';
import TextPrivacyPolicySubTitle from '@/components/material/TextPrivacyPolicySubTitle';
import TextPrivacyPolicyTitle from '@/components/material/TextPrivacyPolicyTitle';
import TextTitle from '@/components/material/TextTitle';

Vue.config.productionTip = false;
Vue.config.devtools = false;

Vue.use(VueRouter);
Vue.use(VueTypedJs);

Vue.component('Divider', Divider);
Vue.component('TextContent', TextContent);
Vue.component('TextHint', TextHint);
Vue.component('TextPrivacyPolicySubTitle', TextPrivacyPolicySubTitle);
Vue.component('TextPrivacyPolicyTitle', TextPrivacyPolicyTitle);
Vue.component('TextTitle', TextTitle);

// Other Components
Vue.component('FlipCountdown', FlipCountdown);

const router = new VueRouter({
  mode: 'hash',
  scrollBehavior () {
    return { x: 0, y: 0 };
  },
  routes: [
    { path: '*', name: 'NotFound', component: NotFound },
    { path: '/', redirect: '/welcome' },
    { path: '/welcome', name: 'Welcome', component: Welcome },
    { path: '/privacy-policy', name: 'PrivacyPolicy', component: PrivacyPolicy }
  ]
});

new Vue({
  vuetify,
  router,
  render: h => h(App)
}).$mount('#app');
